.pagination {
    display: flex;
    justify-content: center;
    background: #edeef0;
    margin-top: 15px;
}
.item {
    padding: 10px 15px;
    border-radius: 5px;
    margin-right: 15px;
    cursor: pointer;
    background: #fff;
}
.active {
    padding: 10px 15px;
    border-radius: 5px;
    margin-right: 15px;
    cursor: pointer;
    background: #fff;
    border: 1px solid steelblue;
}
.next {
    padding: 10px 21px;
    border-radius: 5px;
    margin-right: 15px;
    cursor: pointer;
    background: #fff;
    position: relative;
    &::after {
        content: '';
        background: url('./icons/right-arrow.svg') center / cover no-repeat;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 27px;
        height: 27px;
    }
}
.prev {
    padding: 10px 21px;
    border-radius: 5px;
    margin-right: 15px;
    cursor: pointer;
    background: #fff;
    position: relative;
    &::after {
        content: '';
        background: url('./icons/right-arrow.svg') center / cover no-repeat;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(180deg);
        width: 27px;
        height: 27px;
    }
}