.users {
    background-color: #edeef0;
}
.user {
    background-color: #fff;
    display: flex;
    padding: 10px;
    margin-bottom: 15px;
}
.avatar {
    display: flex;
    flex-flow: column;
    align-items: center;
    img {
        width: 200px;
        height: 200px;
        object-fit: cover;
        border-radius: 50%;
        margin-bottom: 15px;
    }
}
.content {
    padding: 40px;
    width: 100%;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
}
.name {
    font-size: 17px;
    font-weight: 600;
    text-decoration: none;
    color: #000000;
}
.city {
    font-size: 16px;
    font-weight: 300;
}
.first {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
